(function() {
  angular.module('mnoEnterpriseAngular').controller('LandingCtrl', ["$scope", "$rootScope", "$state", "$stateParams", "$window", "MnoeConfig", "MnoeMarketplace", "URI", function($scope, $rootScope, $state, $stateParams, $window, MnoeConfig, MnoeMarketplace, URI) {
    var vm;
    vm = this;
    vm.isLoading = true;
    vm.highlightedApps = [];
    vm.localProducts = [];
    vm.carouselImageStyle = function(app) {
      var picture;
      picture = app.pictures ? app.pictures[0] : app.logo;
      return {
        "background-image": "url(" + picture + ")"
      };
    };
    MnoeMarketplace.getApps().then(function(response) {
      var localHighlightedApp;
      vm.highlightedApps = _.filter(response.apps, function(app) {
        return _.includes(MnoeConfig.publicHighlightedApplications(), app.nid);
      });
      if (MnoeConfig.areLocalProductsEnabled) {
        vm.localProducts = _.filter(response.products, function(product) {
          return product.local && _.includes(MnoeConfig.publicLocalProducts(), product.nid);
        });
        localHighlightedApp = _.filter(response.products, function(product) {
          return _.includes(MnoeConfig.publicHighlightedLocalProducts(), product.nid);
        });
        if (localHighlightedApp) {
          return vm.highlightedApps = vm.highlightedApps.concat(localHighlightedApp);
        }
      }
    })["finally"](function() {
      return vm.isLoading = false;
    });
    vm.highlightHref = function(app) {
      if (app.local) {
        return "public.local_product({productId: app.nid})";
      } else {
        return "public.product({appId: app.nid})";
      }
    };
  }]);

}).call(this);

(function() {
  var indexOf = [].indexOf || function(item) { for (var i = 0, l = this.length; i < l; i++) { if (i in this && this[i] === item) return i; } return -1; };

  angular.module('mnoEnterpriseAngular').controller('MemberAddModalCtrl', ["MnoeTeams", "$uibModalInstance", "Utilities", "team", "availableUsers", function(MnoeTeams, $uibModalInstance, Utilities, team, availableUsers) {
    'ngInject';
    var vm;
    vm = this;
    vm.team = team;
    vm.availableUsers = availableUsers;
    vm.selectedUsers = [];
    vm.isLoading = false;
    vm.toggleUser = function(user) {
      if (indexOf.call(vm.selectedUsers, user) >= 0) {
        return vm.selectedUsers.splice(vm.selectedUsers.indexOf(user), 1);
      } else {
        return vm.selectedUsers.push(user);
      }
    };
    vm.onSubmit = function() {
      vm.isLoading = true;
      return MnoeTeams.addUsers(vm.team.id, vm.selectedUsers).then(function(users) {
        vm.errors = '';
        return $uibModalInstance.close();
      }, function(errors) {
        return vm.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        vm.selectedUsers = [];
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      vm.selectedUsers = [];
      return $uibModalInstance.dismiss('cancel');
    };
  }]);

}).call(this);

